import React, {useContext, useEffect} from "react";
import { Modal } from "react-bootstrap";
import Typography from "@mui/material/Typography";
import { Button } from "@mui/material";
import "./Modal.css";

export default function InfoModal({ open, handleClose, title, subtitle }) {

 

  return (
    <Modal show={open} onHide={handleClose} className="info-modal">
      <Modal.Title className="modal-title">{title}</Modal.Title>

      <Typography id="transition-modal-description" className="modal-text">{subtitle}</Typography>

      <Button variant="contained" onClick={handleClose}>
        Aceptar
      </Button>
    </Modal>
  );
}
